
.search-wrap[data-v-310cd028]{
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 10;
    background: linear-gradient(to bottom,rgba(8,14,37,.95),rgba(0,45,111,.95));
}
.open-wrap[data-v-310cd028]{
    position: absolute;
    box-sizing: border-box;
    top: 0;
    right:0;
    bottom: 0;
    left: 0;
    margin: auto;
    width: 1262px;
    height: 740px;
    /*padding: 90px 65px 56px 55px;*/
    padding: 90px 65px 40px 55px;
    background: url("../../../static/img/search-wrap-bg.png") center no-repeat;
    background-size: 100% 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: stretch;
}
.open-wrap .close[data-v-310cd028]{
    position: absolute;
    /*top: 15px;*/
    /*right: 17px;*/
    /*width: 20px;*/
    /*height: 20px;*/
    top: 8px;
    right: 8px;
    width: 44px;
    height: 44px;
    /*line-height: 15px;*/
    /*color: #ebd91e;*/
    /*font-size: 40px;*/
    cursor: pointer;
    display: flex;
    justify-content: center;
    /*transition: all .5s;*/
    /*background: #34b3b5;*/
    /*box-shadow: 0 0 20px 8px #34b3b5;*/
    background: url("../../../static/img/close-icon.png") center no-repeat;
    transition: all .3s;
}
.open-wrap .close[data-v-310cd028]:hover{
    background: url("../../../static/img/close-icon-hover.png") center no-repeat;
}
.condition-wrap[data-v-310cd028]{
    height: 148px;
    flex: none;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: stretch;
}
.condition-wrap .label-line[data-v-310cd028]{
    box-sizing: border-box;
    border-left: 3px solid #ffcc00;
    padding-left: 10px;
    color: #ffcc00;
    height: 14px;
    font-size: 14px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.condition-wrap .condition[data-v-310cd028]{
    margin-top: 10px;
    flex: auto;
    box-sizing: border-box;
    border: 1px solid #00beff;
    display: flex;
    justify-content: center;
    align-items: stretch;
}
.condition-wrap .condition ul[data-v-310cd028]{
    flex: auto;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    align-content: space-around;
}
.condition-wrap .condition li[data-v-310cd028]{
    flex: none;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 38px;
}
.condition-wrap .condition li .condition-label[data-v-310cd028]{
    width: 75px;
    color: #fff;
    font-size: 14px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.result-wrap[data-v-310cd028]{
    margin-top: 16px;
    flex: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: stretch;
}
.result-wrap  .result-label[data-v-310cd028]{
    color: #fff;
    font-size: 16px;
    flex: none;
    margin-bottom: 15px;
}
.result-wrap  .list-wrap[data-v-310cd028]{
    flex: auto;
    overflow: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: stretch;
    overflow: auto;
    overflow-x: hidden;
    padding-right: 5px;
}
.result-wrap  .list-wrap[data-v-310cd028]:hover::-webkit-scrollbar-thumb{
    background:rgba(7,100,180,1)
}
.result-wrap  .result-count .multiple-wrap[data-v-310cd028]{
    padding: 0 12px;
    height: 35px;
    flex: none;
    background: rgba(3,76,126,.5);
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.result-wrap  .result-count .multiple-label[data-v-310cd028]{
    flex: none;
    width: 86px;
    height: 24px;
    border: 1px solid #00cbff;
    color: #fff;
    font-size: 14px;
    background: linear-gradient(#029ed2,#0178d2);
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 15px;
}
.result-wrap  .result-count .multiple-condition[data-v-310cd028]{
    flex: auto;
    cursor: default;
    color: #e2e2e2;
    font-size: 14px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.result-wrap  .result-count .condition-value[data-v-310cd028]{
    color: #fff600;
    margin-left: 10px;
}
.result-wrap  .result-count .totalRecord[data-v-310cd028]{
    color: #fff;
    font-size: 12px;
    text-align: right;
    margin: 8px 0;
}
.result-wrap  .result-count .resultCount[data-v-310cd028]{
    color: #d5d123;
}
.result-wrap  .list-wrap ul[data-v-310cd028]{
    flex: auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
}
.result-wrap  .list-wrap li[data-v-310cd028]{
    cursor: pointer;
    padding-left: 12px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
    height: 80px;
    margin-bottom: 6px;
    flex: none;
    border: 5px solid transparent;
    border-left: 1px solid transparent;
    border-right:1px solid transparent;
    border-image: url("../../../static/img/result-list-bg.png") 5 1 5 1 round;
    background: rgba(3,70,123,.5);
}
.result-wrap  .list-wrap li.noDataLi[data-v-310cd028]{
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-size: 18px;
}
.result-wrap  .list-wrap li span[data-v-310cd028]{
    color: #fff;
    font-size: 12px;
}
.result-wrap  .list-wrap li .entName[data-v-310cd028]{
    color: #fff600;
    font-size: 16px;
    cursor: pointer;
}
.result-wrap  .list-wrap li .registerTime[data-v-310cd028]{
    margin: 0 22px;
}
.result-wrap  .list-wrap li[data-v-310cd028]:hover{
    background: rgba(3,70,123,.8);
}
.pageWrap[data-v-310cd028]{
    padding-top: 10px;
}



