
@import 'assets/css/common.css';
/*清算信息*/
.clearingInfo[data-v-179701e8]{
    width:100%;
}
.clearingInfo .listItem[data-v-179701e8]{
    box-sizing:border-box;
    margin:2px 4px 2px 0;
    width:calc((100% - 8px) / 3 );
    height:34px;
    /*background:#0d2c58;*/
    /*opacity: 0.8;*/
    background:rgba(88,143,255,0.15);
    color: #ffffff;
    border:1px solid #1e417a;
}
.clearingInfo .listItem[data-v-179701e8]:hover{
    background: rgba(40,92,198,0.35);
}
.clearingInfo .marginRightZero[data-v-179701e8]{
    margin:2px 0px 2px 0;
}
.clearingInfo .listItem span[data-v-179701e8]{
    display: inline-block;
    border-right:1px solid #1e417a;
    height:16px;
    margin-top:8px;
}
.clearingInfo .listItem .borderRightNone[data-v-179701e8]{
    border-right:none;
}
.clearingInfo .listItem .num[data-v-179701e8]{
    width:calc(15% - 8px);
    text-align: center;
}
.clearingInfo .listItem .position[data-v-179701e8]{
    width:calc((85% - 8px) / 2);
    text-align: center;
}
.clearingInfo .listItem .name[data-v-179701e8]{
    width:calc((85% - 8px) / 2);
    text-align: center;
}




