.nav-list[data-v-32ea61ec] {
    position: relative;
    box-sizing: border-box;
    height: 40px;
    line-height: 40px;
    width: 100%;
    overflow: hidden;
}
.nav-list[data-v-32ea61ec]::before{
    box-sizing: border-box;
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 50px;
    height: 100%;
    background: linear-gradient(to right,rgba(18,25,48,1),rgba(18,25,48,0));
    z-index: 1;
}
.nav-list[data-v-32ea61ec]::after{
    box-sizing: border-box;
    position: absolute;
    content: '';
    top: 0;
    right: 0;
    width: 50px;
    height: 100%;
    background: linear-gradient(to left,rgba(18,25,48,1),rgba(18,25,48,0));
    z-index: 1;
}
