}
.tenShareholders td.classA[data-v-c550fd70]{
   color:#fff;
}
.tenShareholders td.classB[data-v-c550fd70]{
    color:#e07567;
}
.tenShareholders td.classB[data-v-c550fd70]:after{
    content:" ↑";
